:root{
  --pixel-size: 0.6;
  --move-time:1;
}

.character{
  z-index: 10;
  position: relative;
  width: calc(4.79rem * 0.6);
  width: calc(4.79rem * var(--pixel-size));
  height: calc(6.4rem * 0.6);
  height: calc(6.4rem * var(--pixel-size));
  overflow: hidden;
}

.character-sprite-sheet{
  position: absolute;
  width: calc(20rem * 0.6);
  width: calc(20rem * var(--pixel-size));
  -webkit-animation:  moveSpriteSheet steps(4) calc(0.5s * 1)   infinite;
          animation:  moveSpriteSheet steps(4) calc(0.5s * 1)   infinite;
  -webkit-animation:  moveSpriteSheet steps(4) calc(0.5s * var(--move-time) )   infinite;
          animation:  moveSpriteSheet steps(4) calc(0.5s * var(--move-time) )   infinite;
}

.shadow{
  position: absolute;
  width: calc(4rem * 0.6);
  width: calc(4rem * var(--pixel-size));
  height: calc(1rem * 0.6);
  height: calc(1rem * var(--pixel-size));
  top: calc(5.6rem * 0.6);
  top: calc(5.6rem * var(--pixel-size));
  left: calc(0.5rem * 0.6);
  left: calc(0.5rem * var(--pixel-size));
}

.pixelArt {
  -ms-interpolation-mode: nearest-neighbor;
      image-rendering: -moz-crisp-edges;
      image-rendering: pixelated;
}

@-webkit-keyframes moveSpriteSheet {
  from {
    -webkit-transform: translate3d(0%, 0, 0);
            transform: translate3d(0%, 0, 0);
  }
  to {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

@keyframes moveSpriteSheet {
  from {
    -webkit-transform: translate3d(0%, 0, 0);
            transform: translate3d(0%, 0, 0);
  }
  to {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
  }
}

.stand{
  -webkit-animation: none;
          animation: none;
  -webkit-transform: translate3d(-3rem, 0px, 0);
          transform: translate3d(-3rem, 0px, 0);
}

.right{
  top: -3.96rem;
  left: 0.3rem;
}

.left{
  top: -8rem;
  left: 0.1rem;
}

.up{
  left: -0.04rem;
  top: -12rem;
}
.x-container{
  position: relative;
  overflow-x: hidden;
  height: 100%;
}

.svg-line{
